export default {
	data() {
		return {
			heightt:'',
			hnum:600,
			vote:false
		};
	},
	onLoad(option) {
		// 修改标题
		// uni.setNavigationBarTitle({
		//     title: '新的标题'
		// });
		// 根据参与人数动态设置高度
		this.hnum=550*3
		// this.$refs.xxx.style.height= '1150rpx';
		this.heightt = 'height:'+this.hnum+ "rpx;"		
		uni.request({
		   url: 'https://www.baidu.com/', //仅为示例，并非真实接口地址。
		   method: "POST",
		   header: {
		   	'Content-Type': 'application/json'
		   	// 'X-ECTouch-Authorization': token
		   },
		   success: (res) => {// 返回的数据
		       console.log(res.data);
		       // this.text = 'request success';
		   }
		});	
			
		
	},
	methods: {
		goto(){
			uni.navigateTo({
				url:"../video/video?video_url="+"https://img.cdn.aliyun.dcloud.net.cn/guide/uniapp/%E7%AC%AC1%E8%AE%B2%EF%BC%88uni-app%E4%BA%A7%E5%93%81%E4%BB%8B%E7%BB%8D%EF%BC%89-%20DCloud%E5%AE%98%E6%96%B9%E8%A7%86%E9%A2%91%E6%95%99%E7%A8%8B@20200317.mp4"
			})
		},
		getH(){
			this.hnum=550*3
			// this.$refs.xxx.style.height= '1150rpx';
			this.heightt = 'height:'+this.hnum+ "rpx;"	
		},
		fenlie(){
			let list = [1,2,3,4,5,6,7,8,9]
			let leftList =[];
			let rightList = [];
			list.forEach((item,index)=>{
				if(index%2==1){
					leftList.push(item)
				}else{
					rightList.push(item)
				}
			})
		},
		voteit(){		
			if(this.vote){
				uni.showToast({
					title:'你已经投过了',
					duration:2000,
					icon:'none'
				})
			}else{
				this.vote = true
				uni.showToast({
					title:'投票成功',
					duration:2000
				})
				uni.request({
				    url: 'https://www.baidu.com/', //仅为示例，并非真实接口地址。
				    data: {// 请求参数
				        text: 'uni.request' 
				    },
					method: "POST",
					header: {
						'Content-Type': 'application/json'
						// 'X-ECTouch-Authorization': token
					},
				    success: (res) => {// 返回的数据
				        console.log(res.data);
				        // this.text = 'request success';
				    }
				});	
			}
			
		}
	}
};
